













import Vue from 'vue'
import TimeCalculator from '@/components/TimeCalculator.vue'
import TimeSlot from '@/domain/TimeSlot'

export default Vue.extend({
    components: {
        TimeCalculator,
    },
    data() {
        return {
            result: new TimeSlot(-1, -1),
            valid: false,
        }
    },
    methods: {
        onResult(result: TimeSlot) {
            this.result = result
        },
    },
})
